@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://db.onlinewebfonts.com/c/52f2359a557a3d980b1b3bc62a32aa8a?family=Mokgech+Regular");

body {
  font-family: "Kanit", sans-serif;
}

.gradient {
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5) 60%,
    rgba(255, 255, 255, 0.5)
  );
}
.bg-color {
  background-image: linear-gradient(to bottom, #ffcc00, #ffa800);
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.5);
}
.gradient-gold {
  background: linear-gradient(
    to top left,
    rgba(0, 0, 0, 0.58) 0%,
    /* Black color with 58% opacity to the top left */ rgba(255, 168, 0, 0.6)
      20%,
    /* Yellow color with 50% opacity to the top right */ rgba(255, 168, 0, 0.6)
      40%,
    /* Yellow color with 50% opacity to the bottom right */ rgba(0, 0, 0, 0.58)
      65%,
    /* Black color with 58% opacity to the bottom left */ rgba(0, 0, 0, 0.58)
      100% /* Additional black color with 58% opacity to the bottom left */
  );
}

.gradient:hover {
  background: linear-gradient(
    to bottom,
    rgba(255, 204, 0, 0.8) 10%,
    rgba(255, 168, 0, 0.8) 40%,
    rgba(255, 255, 255, 0.2) 80%,
    rgba(0, 0, 0, 0.8)
  );
}

.font-mok {
  font-family: "Mokgech Regular";
}

.main-container {
  background-image: url(img/main_container.png);
  background-size: 100% 100%;
  height: 72vh;
  min-width: 100vh;
}

.logo-container {
  background-image: url(img/logo_border.png);
  border-radius: 1vh;
  box-shadow: 0.5px 0 0.5px rgba(0, 0, 0, 0.2),
    -0.5px 0 0.5px rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
  background-size: 100% 100%; /* พอดีกับคอนเทนเนอร์โดยไม่ crop */
  height: 16vh;
}
.border-img {
  border: 1vh solid transparent;
  border-image: url(img/item_border.png) 50 round;
  border-left: none; /* หรือ border-left: 0; */
  height: 15.5vh;
}

.border-items {
  border: 1vh solid transparent;
  border-image: url(img/item_border.png) 50 round;
}

.point-container {
  background-image: url(img/point.png);
  background-size: 100% 100%;
}

.btn-img {
  background-image: url(img/button.png);
  background-size: 100% 100%;
  background-position: center;
  height: 2.8vh;
}

button:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.6);
  transition: transform 0.3s ease;
  border-radius: 0.8vh;
}

-webkit-scrollbar {
  display: none;
}
